import React, { useState } from 'react';
import { Link } from 'gatsby';
import { FaDiscord } from 'react-icons/fa';
import logo from '@/assets/img/logo.svg';
import NavbarItem from './NavbarItem';
import { ToggleButton } from './ToggleButton';

export default function Navbar() {
  const [isNavbarOpen, setNavbarState] = useState(false);

  return (
    <div
      className="fixed top-0 left-0 w-full z-30 duration-300"
      style={{ backgroundColor: '#000318' }}
    >
      <div className="container">
        <nav className="navbar py-2 navbar-expand-lg flex justify-between items-center relative duration-300">
          <Link className="navbar-brand" to="/">
            <img className="h-7" src={logo} alt="Logo" />
          </Link>

          <ToggleButton onToggle={() => setNavbarState(!isNavbarOpen)} />

          <div
            className={`${
              !isNavbarOpen ? 'hidden' : ''
            } collapse lg:block duration-300 shadow absolute left-0 mt-full bg-white z-20 px-5 py-3 lg:static lg:bg-transparent lg:shadow-none`}
          >
            <ul className="navbar-nav mr-auto justify-center items-center lg:flex">
              <NavbarItem text="Home" href="/" />
              <NavbarItem
                text={
                  <>
                    <FaDiscord className="inline-block w-5 h-5" /> Join Discord
                  </>
                }
                href="https://discord.gg/sbfen8Y8"
              />
            </ul>
          </div>
          <div className="header-btn hidden sm:block sm:absolute sm:right-0 sm:mr-16 lg:static lg:mr-0">
            <Link
              className="text-white border border-blue-600 px-10 py-3 rounded-full duration-300 hover:bg-blue-600 hover:text-white dark-bg-shadow"
              to="/start-company"
            >
              Start a company
            </Link>
          </div>
        </nav>
      </div>
    </div>
  );
}
